import React from 'react'

const Footer = () => {
  const styles = {
    bottom: 0,
    height: '5em',
    position: 'absolute',
  }

  return (
    <footer id={ 'Footer' } style={ styles }>
      <p><small>{ '' }</small></p>
    </footer>
  )
}

export default Footer



